// ./ProfileSection.tsx
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { useMutation } from "@apollo/client";
import { Edit2, Save, X } from "lucide-react";
import { useState } from "react";

export default function ProfileSection() {

  const [
    updateUserProfileDetails,
    {
      loading: updateUserProfileDetailsLoading,
      error: updateUserProfileDetailsError,
    },
  ] = useMutation(UPDATE_USER_PROFILE_DETAILS, {
    refetchQueries: [{ query: GET_USER_PROFILE_DETAILS }],
    awaitRefetchQueries: true,
  });

  if (updateUserProfileDetailsLoading) console.log("Loading user profile");

  if (updateUserProfileDetailsError)
    console.log("User profile Update error", userProfileDetailsError);



  const [isEditing, setIsEditing] = useState(false);
  const [form, setForm] = useState(user);

  // console.log("user",user)


  const handleSave = async () => {
    console.log("user-->",form)
  await updateUserProfileDetails({
    variables: {
      input: {
        firstName: form.firstName,
        lastName: form.lastName,
        phone: form.phone,
        gender: form.gender,
        dob: form.dob ? new Date(form.dob).toISOString() : null,
      },
    },
  });

  setIsEditing(false);
};


  return (
    <Card>
      <CardHeader>
        <div className="flex items-center justify-between">
          <CardTitle>Profile Information</CardTitle>
          <Button
            variant={isEditing ? "destructive" : "outline"}
            size="sm"
            onClick={() => setIsEditing(!isEditing)}
          >
            {isEditing ? <X size={16} /> : <Edit2 size={16} />}
            {isEditing ? "Cancel" : "Edit"}
          </Button>
        </div>
      </CardHeader>

      <CardContent className="space-y-6">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div>
            <label className="block text-sm font-medium mb-2">First Name</label>
            <Input
              value={form?.firstName || ""}
              onChange={(e) => setForm({ ...form, firstName: e.target.value })}
              disabled={!isEditing}
              placeholder="Enter first name"
            />
          </div>
          <div>
            <label className="block text-sm font-medium mb-2">Last Name</label>
            <Input
              value={form?.lastName || ""}
              onChange={(e) => setForm({ ...form, lastName: e.target.value })}
              disabled={!isEditing}
              placeholder="Enter last name"
            />
          </div>
          <div>
            <label className="block text-sm font-medium mb-2">Email</label>
            <Input
              value={form?.email}
              disabled={true}
              type="email"
              placeholder="Your email address"
            />
          </div>
          <div>
            <label className="block text-sm font-medium mb-2">Phone</label>
            <Input
              value={form?.phone || ""}
              onChange={(e) => setForm({ ...form, phone: e.target.value })}
              disabled={!isEditing}
              placeholder="Enter phone number"
            />
          </div>
          <div>
            <label className="block text-sm font-medium mb-2">Gender</label>
            <Select disabled={!isEditing}>
              <SelectTrigger>
                <SelectValue placeholder="Select gender" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="male">Male</SelectItem>
                <SelectItem value="female">Female</SelectItem>
                <SelectItem value="other">Other</SelectItem>
                <SelectItem value="prefer-not-to-say">
                  Prefer not to say
                </SelectItem>
              </SelectContent>
            </Select>
          </div>
          <div>
            <label className="block text-sm font-medium mb-2">
              Date of Birth
            </label>
            <Input type="date" disabled={!isEditing} />
          </div>
        </div>
        {isEditing && (
          <div className="flex justify-end space-x-3">
            <Button variant="outline" onClick={() => setIsEditing(false)}>
              Cancel
            </Button>
            <Button onClick={handleSave}>
              <Save size={16} className="mr-2" />
              Save Changes
            </Button>
          </div>
        )}
      </CardContent>
    </Card>
  );
}
